import React, { useState } from "react"
// import Popup from "reactjs-popup";
import { useSpring, animated } from 'react-spring';
// import { url } from "react";
import BackImage1 from "../assets/MeSilhouetteBeach.jpg";

export default function ProjectCard(props) {

    var Skills = () => {
        var skills = [];
        for (var s in props.contt["Skills"]) {
            skills.push(
                <div class="skillboxlet">
                    {props.contt["Skills"][s]}
                </div>)
        }
        return skills;
    };

    const progCardSumStyle = {
        gridRow: "2/3",
        gridColumn: "1/3",
    }

    const theImage = {
        // border: "2px solid red",
        backgroundImage: `url(${props.contt["Image"]})`,
        // backgroundImage: `${BackImage1}`,
        // backgroundImage: "url(../assets/MeSilhouetteBeach.jpg)",

        backgroundPosition: "center",
        backgroundSize: "contain",
        backgroundRepeat: "no-repeat",

    }
    // style={theImage}  bgImage= {BackImage1}
    const card = <div class="projectcard hoverGrow" style={theImage} bgImage= {BackImage1} >
        <div>
            {/* <img style={{ width: "100%", height: "100%" }} src="../assets/MeSilhouetteBeach.jpg" /> */}

        </div>
        <div class="projectcardskills">
            {Skills()}
        </div>

        <div class="fillit" style={progCardSumStyle}>
            {props.children}
            <h1> {props.contt["Title"]} </h1>
            <p>{props.contt["Summary"]}</p>
        </div>
    </div>


    const expandedSumStyle = {
        gridRow: "1/3",
        gridColumn: "1/3",
    }

    const reader = () => {
        let arr = [];
        let len = Object.keys(props.contt["Details"]).length;
        for (let i = 1; i < len; i++) {
            arr.push(
                <li>{props.contt["Details"][i]}</li>
            )
        }
        return arr;
    }
    const window = <div class="projectcard hoverGrow" style={{ zIndex: "0" }, theImage}>

        <div class="fillit" style={expandedSumStyle} >
            {props.children}
            <h1> {props.contt["Title"]} </h1>
            <p>{props.contt["Details"][0]}</p>
            {console.log("num details: ", Object.keys(props.contt["Details"]).length)}
            {reader()}
        </div>
        <div class="projectcardskills" style={{ gridColumn: "1/3" }}>
            {Skills()}
        </div>
    </div>

    const [flipped, set] = useState(true)

    const { transform, opacity } = useSpring({
        opacity: flipped ? 1 : 0,
        transform: `perspective(600px) rotateX(${flipped ? 180 : 0}deg)`,
        config: { mass: 2, tension: 500, friction: 50 }
    })

    return <div onClick={() => set(state => !state)}>
        <div style={{ display: "grid", gridTemplateRows: "1" }}>
            <div style={{
                gridRow: "1/2",
                gridColumn: "1/2"
            }}>
                <animated.div style={{ opacity: opacity.interpolate(o => 1 - o), transform }} >{window}</animated.div>
            </div>
            <div style={{
                gridRow: "1/2",
                gridColumn: "1/2"
            }}>
                <animated.div style={{ opacity, transform: transform.interpolate(t => `${t} rotateX(180deg)`) }}>{card}</animated.div>
            </div>
        </div>
    </div >
        ;
}