import React, { useState } from "react"
import Popup from "reactjs-popup";

export default function ProjectCard(props) {
    const [spread, opener] = useState(false);
    // let loggg = () => {
    //     console.log("opener is ", spread);
    //     opener(false);
    //     console.log("opener is ", spread);
    // }

    const cardStyle = {
        // backgroundColor: `rgba(255, 255, 255, calc(${props.contt["Confidence"]}*.2))`,
        // width: `calc(3vmax + ${props.contt["Confidence"]}*2vmax)`,
        // height: `calc(3vmax + ${props.contt["Confidence"]}*1vmax)`,
        width: '8vmax',
        height: '6vmax',
        backgroundColor: "rgba(255, 255, 255, .001)",
        // border:"2px solid red",

        backgroundImage: `${props.contt["logo"]}`,
        backgroundPosition: "center",
        backgroundSize: "contain",
        backgroundRepeat: "no-repeat"

    }


    const card = <div class="skillcard hoverGrow" style={cardStyle}>
        {/* {props.contt["Name"]} */}
    </div>;

    const projectboxlet = {
        color: "white",
        backgroundColor: "rgba(255, 81, 0, 0.884)",
        boxSizing: "border-box",
        padding: "5px",
        margin: "5px",
        borderRadius: "5px"
    }

    var Skills = () => {
        var skills = [];
        for (var s in props.contt["Projects"]) {
            skills.push(
                <div style={projectboxlet}>
                    {props.contt["Projects"][s]}
                </div>)
        }
        return skills;
    };


    const innerboxstyle = {
        color: "rgb(15, 20, 27)",
        padding: "10px",
        backgroundColor: "white",
        borderRadius: "10px",
        width: "300px",
        height: "100%",
        boxSizing: "border-box",
        alignItems: "center",
        // justifyContent: "center",
    }



    return <Popup trigger={card} position="center center" arrow={false} open={spread} closeOnDocumentClick>
        <div style={innerboxstyle}>
            <div >
                <a onClick={async () => { opener(!spread); }} style={{ float: "right" }}> &times; </a>
                <h3> {props.contt["Name"]} </h3>
            </div>
            {props.contt["Opinion"]}
            <div class="projectcardskills" >
                {Skills()}
            </div>
        </div>
    </Popup>;
}