import React, { useEffect } from "react"
import Headfoot from "../components/headfoot";
import { Parallax } from 'react-parallax';
import Resume from "../assets/Resume_8.8.20.pdf"
import Skills from "../components/skills";
import Projects from "../components/projects";
import BackImage1 from "../assets/MeSilhouetteBeach.jpg";
export default function Home() {
  let location = "Sumanth Aluri";
  // const Projects;
  // if (typeof window !== `undefined`) {
  //   Projects = require("../components/projects")
  // }

  // useEffect(()=>{Projects = require("../components/projects")})
  const fillitgreyStyle = {
    backgroundColor: "rgba(49, 49, 49, 0.9)",
    color: "white",
    margin: "10vmin",
    padding: "20px",
    width: "fit-content",
    // boxSizing: "border-box",
    overflow: "hidden",
    borderRadius: "10px",
    height: "fit-content"
  }
  const centerUnderline = {
    textAlign: "center",
    padding: "10px",
    borderBottom: "1px solid red",
    width: "fit-content"
  }

  const pageBlock = {
    height: "85vh",
    paddingTop: "10vh",
    paddingBottom: "5vh",
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    alignItems: "center"
  }

  const NavBlockStyle = { borderRadius: "10px", backgroundColor: "rgba(170, 29, 29, 0.9)", textAlign: "center", padding: "1vh", color: "white", width: "20vw" }

  return (
    <Headfoot location={location} >
      <div>
        <Parallax bgImage={BackImage1} strength={600} blur={{ min: -10, max: 20 }}>
          <div style={pageBlock}>
            <div style={fillitgreyStyle}>
              <h1 style={{ textAlign: "center", fontFamily: "Arial" }}>Hello</h1>
              <p style={{ fontSize: "18px" }}>
                I'm a simple college student with a pation for development. My relationship with technology can be condensed into two points:
              </p>
              <li style={{ fontSize: "18px" }}>
                Learning new Technologies is simply fun, weather it be a new language or a new framework. Its interseting to see how different technologies solve problems differently.
              </li>
              <br />
              <li style={{ fontSize: "18px" }}>
                I think Software development is amazing, it gives me the tools to make products that directly impact the world. Weather it be a game that people play or a service that helps companies do business.
              </li>
            </div>
          </div>
        </Parallax>


        <div class="horizontalcolumn " style={{ height: "fit-content", paddingTop: "10vh", border: "2px solid red" }}>

          <div style={{ padding: "10vmin" }}>
            <div style={{ textAlign: "center", fontStyle: "italics", fontSize: "25px" }}> Further below are a selection of my past projects, but Github is a great place to see what I have been working on recently.</div>
            <div style={{ padding: "10px", display: "flex", justifyContent: "space-around", width: "80%" }}>
              <p style={{ fontSize: "25px" }}> Feel free to reach out anytime: </p>
              <h2 style={{}}>    aluris@ufl.edu</h2>
              <h2 style={{}}>    (904) 479-3394</h2>
            </div>
          </div>

          <div class="horizontalcolumn" style={{
            // border:"2px solid red",
            flexGrow: "1",
            minWidth: "50vw",
          }}>
            <a href={Resume} target="_blank" rel="noreferrer">
              <h1 class="hoverGrow" style={NavBlockStyle}>Resume</h1>
            </a>
            <br></br>
            <a href={"https://github.com/manthi4"} target="_blank" rel="noreferrer">
              <h1 class="hoverGrow" style={NavBlockStyle}>Github</h1>
            </a>
          </div>
        </div>

        <Parallax bgImage={BackImage1} strength={600} blur={{ min: -10, max: 20 }}>
          <div class="blurr" style={pageBlock}>
            <h1 style={centerUnderline}> Technologies I've worked with</h1>
            <h3 style={{ maxWidth: "90vw" }}> I like to learn the basics about how things work and then jump into working on a fun project using that technology. I have used all if these Technologies in one project or another. </h3>
            <Skills></Skills>
          </div>
        </Parallax>

        <Parallax strength={600} blur={{ min: -10, max: 20 }}>
          <div style={{ height: "5vh" }}></div>
          <div style={{ display: "flex", justifyContent: "center" }}>
            <h1 style={centerUnderline}>My past projects</h1>
          </div>
          <Projects></Projects>

        </Parallax>
      </div>
    </Headfoot>
  );
}