import React, { useEffect } from "react"
// import Headfoot from "./headfoot";
import ProjectCard from "./ProjectCard"
// import Skills from './skills';
import projects from '../assets/projects.json'

export default function Projects() {
    // let location = "My Projects";
    // let background = 'MeSilhouetteBeach.jpg';


    let ProjectCards = () => {
        let skills = [];
        for (var s in projects) {
            skills.push(
                <ProjectCard to="/" title="Home" contt={projects[s]}></ProjectCard>
            )
        }
        console.log(skills);
        return skills;
    };

    console.log("these are the cards: " + ProjectCards())

    let mobileStyle = { padding: "3vmin"}
    const wininW = typeof window !== 'undefined' && window.innerWidth;

    console.log('inner width ', window.innerWidth < 600)
    if(wininW < 600){
        mobileStyle = {
            gridTemplateColumns: "repeat(auto-fit, minmax(200px, 1fr))"
        }
    }
    

    return (
        <div class="grid" style = {mobileStyle}>
            {ProjectCards()}
        </div>
    )
}