import React from "react"
import SkillCard from "../components/SkillCard"
import skills from "../assets/skills.json";

export default function Skills() {
    // let location = "Skills";

    var SkillCards = () => {
        var skillcards = [];
        for (var s in skills) {
            skillcards.push(
                <SkillCard to="/" contt={skills[s]}></SkillCard>
            )
        }
        console.log(skills);
        return skillcards;
    };

    const skillsgrid = {
        backgroundColor: "rgba(49, 49, 49, 0.9)",
        borderRadius: "20px",
        margin: "10px",

        display: "grid",
        gridTemplateColumns: "repeat(auto-fit, minmax(12vmax, 1fr))",
        gridGap: "1em",
        width: "95vw",
        justifyItems: "center",
        alignItems: "center",
        padding: "20px",
        boxSizing: "border-box"
    }

    return (
        <div style={skillsgrid}>
            {SkillCards()}
        </div>
    )
}

